<template>
  <v-layout align-start>
    <v-flex>
      <v-data-table
        :headers="headers"
        :items="ventas"
        :search="search"
        v-if="verNuevo == 0"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-toolbar-title>Ventas</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>

            <v-text-field
              class="text-xs-center"
              v-model="search"
              append-icon="search"
              label="Búsqueda"
              single-line
              hide-details
            ></v-text-field>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-if="verNuevo == 0"
              @click="mostrarNuevo()"
            >
              Nuevo
            </v-btn>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="headline"
                  >Seguro que quieres borrar este item?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="secondary" text @click="close, limpiar()"
                    >Cancel</v-btn
                  >
                  <v-btn color="success" text @click="guardar">Guardar</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="adModal" max-width="290px">
              <v-card>
                <v-card-title class="headline" v-if="adAccion == 1">
                  Activar Item
                </v-card-title>
                <v-card-title class="headline" v-if="adAccion == 2">
                  Desactivar Item
                </v-card-title>
                <v-card-text>
                  Estas a punto de
                  <h4 v-if="adAccion == 1">Activar</h4>
                  <h4 v-if="adAccion == 2">Desactivar</h4>
                  el item {{ adNombre }}
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn
                    @click="activarDesactivarCerrar()"
                    text
                    color="secondary"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    v-if="adAccion == 1"
                    @click="activar()"
                    text
                    color="success"
                  >
                    Activar
                  </v-btn>
                  <v-btn
                    v-if="adAccion == 2"
                    @click="desactivar()"
                    text
                    color="error"
                  >
                    Desactivar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="comprobanteModal" max-width="800px" persistent>
              <v-card>
                <v-card-title>
                  Reporte de Ventas
                  <v-spacer></v-spacer>
                  <template>
                    <v-btn class="ml-5 mr-2" color="success"
                      ><v-icon title="Descargar PDF" @click="crearPDF()"
                        >picture_as_pdf</v-icon
                      ></v-btn
                    >
                    <v-btn dark color="error" @click="ocultarComprobante()"
                      >X</v-btn
                    >
                  </template>
                </v-card-title>

                <v-card-text>
                  <div id="factura">
                    <header>
                      <v-avatar id="logo" size="90">
                        <img
                          src="data:image/png;base64,"
                          id="imagen"
                        />
                      </v-avatar>
                      <div id="datos">
                        <p id="encabezado">
                          <b>IncanatoIT</b>
                          <br />José Gálvez 1368, Chongoyape - Chiclayo, Perú<br />Telefono:(+58)931742904<br />Email:gabriubusy@gmail.com
                        </p>
                      </div>
                      <div id="fact">
                        <p>
                          {{ tipo_comprobante }}<br />
                          {{ serie_comprobante }}-{{ num_comprobante }}<br />
                          {{ fecha }}
                        </p>
                      </div>
                    </header>
                    <br />
                    <section>
                      <div>
                        <table id="facliente">
                          <tbody>
                            <tr>
                              <td id="cliente">
                                <strong>Sr(a). {{ persona.nombre }}</strong
                                ><br />
                                <strong>Documento:</strong>
                                {{ persona.num_documento }}<br />
                                <strong>Dirección:</strong>
                                {{ persona.direccion }}<br />
                                <strong>Teléfono:</strong> {{ persona.telefono
                                }}<br />
                                <strong>Email:</strong> {{ persona.email }}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </section>
                    <br />
                    <section>
                      <div>
                        <table id="facarticulo">
                          <thead>
                            <tr id="fa">
                              <th>CANT</th>
                              <th>DESCRIPCION</th>
                              <th>PRECIO UNIT</th>
                              <th>DESC.</th>
                              <th>PRECIO TOTAL</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-for="det in detalles" :key="det._id">
                              <td style="text-align:center;">
                                {{ det.cantidad }}
                              </td>
                              <td>{{ det.articulo }}</td>
                              <td style="text-align:right;">
                                {{ det.precio }}
                              </td>
                              <td style="text-align:right;">
                                {{ det.descuento }}
                              </td>
                              <td style="text-align:right;">
                                {{
                                  (
                                    det.cantidad * det.precio -
                                    det.descuento
                                  ).toFixed(2)
                                }}
                              </td>
                            </tr>
                          </tbody>
                          <tfoot>
                            <tr>
                              <th></th>
                              <th></th>
                              <th></th>
                              <th style="text-align:right;">SUBTOTAL</th>
                              <th style="text-align:right;">
                                $
                                {{
                                  (totalParcial = (
                                    total - totalImpuesto
                                  ).toFixed(2))
                                }}
                              </th>
                            </tr>
                            <tr>
                              <th></th>
                              <th></th>
                              <th></th>
                              <th style="text-align:right;">
                                IVA({{ impuesto }}%)
                              </th>
                              <th style="text-align:right;">
                                $
                                {{
                                  (totalImpuesto = (
                                    (total * impuesto) /
                                    (1 + impuesto)
                                  ).toFixed(2))
                                }}
                              </th>
                            </tr>
                            <tr>
                              <th></th>
                              <th></th>
                              <th></th>
                              <th style="text-align:right;">TOTAL</th>
                              <th style="text-align:right;">
                                $ {{ (total = calcularTotal).toFixed(2) }}
                              </th>
                            </tr>
                          </tfoot>
                        </table>
                      </div>
                    </section>
                    <br />
                    <br />
                    <footer>
                      <div id="gracias">
                        <p><b>Gracias por su compra!</b></p>
                      </div>
                    </footer>
                  </div>
                </v-card-text>

                <v-card-actions> </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>

        <template v-slot:item.estado="{ item }">
          <h4 v-if="item.estado" class="green--text">Aceptado</h4>
          <h4 v-else class="red--text">Anulado</h4>
        </template>
        <template v-slot:item.opciones="{ item }">
          <td class="justify-center layout px-0">
            <v-icon
              small
              title="Ver Detalles"
              class="mr-2"
              @click="verIngreso(item)"
            >
              tab
            </v-icon>
            <v-icon
              small
              title="Ver Factura"
              class="mr-2"
              @click="mostrarComprobante(item)"
            >
              picture_as_pdf
            </v-icon>
            <template v-if="item.estado">
              <v-icon
                small
                title="Desactivar"
                @click="activarDesactivarMostrar(2, item)"
              >
                block
              </v-icon>
            </template>
            <template v-else>
              <v-icon
                small
                title="Activar"
                @click="activarDesactivarMostrar(1, item)"
              >
                check
              </v-icon>
            </template>
          </td>
        </template>
      </v-data-table>
      <v-dialog v-model="dialog" max-width="1000px">
        <v-card>
          <v-card-title>
            <span class="hidline">Seleccione un Articulo</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm12 md12 lg12 xl12>
                  <v-text-field
                    class="text-xs-center"
                    v-model="texto"
                    append-icon="search"
                    label="Búsqueda"
                    @keyup.enter="listarArticulos()"
                  ></v-text-field>
                  <v-spacer></v-spacer>
                  <template>
                    <v-data-table
                      :headers="cabeceraArticulo"
                      :items="articulos"
                      class="elevation-1"
                    >
                      <template v-slot:item.estado="{ item }">
                        <h4 v-if="item.estado" class="green--text">Activo</h4>
                        <h4 v-else class="red--text">Inactivo</h4>
                      </template>
                      <template v-slot:item.seleccionar="{ item }">
                        <td class="justify-center layout px-0">
                          <v-btn color="success" small
                            >Agregar
                            <v-icon
                              small
                              title="Añadir"
                              @click="agregarDetalle(item)"
                            >
                              add
                            </v-icon></v-btn
                          >
                        </td>
                      </template>
                    </v-data-table>
                  </template>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="secondary" dark @click="close">
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <template v-if="verNuevo == 1">
        <v-card>
          <v-card-title class="ml-5">
            <span v-if="verDetalle == 0" class="hidline ml-5"
              >Creación de Factura</span
            >
            <span v-if="verDetalle == 1" class="hidline ml-5"
              >Detalles de Factura</span
            >
          </v-card-title>
          <v-spacer></v-spacer>
          <v-container grid-list-sm class="pa-4 white">
            <v-layout row wrap>
              <v-flex xs12 sm4 md4 lg4 x14>
                <v-select
                  v-if="verDetalle == 0"
                  v-model="tipo_comprobante"
                  :items="comprobantes"
                  label="Tipo comprobante"
                ></v-select>
                <span v-if="verDetalle == 1">
                  TIPO DE COMPROBANTE:<span class="green--text">{{
                    tipo_comprobante
                  }}</span>
                </span>
              </v-flex>
              <v-flex xs12 sm4 md4 lg4 x14>
                <v-text-field
                  v-if="verDetalle == 0"
                  v-model="serie_comprobante"
                  label="Serie comprobante"
                ></v-text-field>
                <span v-if="verDetalle == 1">
                  SERIE DE COMPROBANTE:<span class="green--text">{{
                    serie_comprobante
                  }}</span>
                </span>
              </v-flex>
              <v-flex xs12 sm4 md4 lg4 x14>
                <v-text-field
                  v-if="verDetalle == 0"
                  v-model="num_comprobante"
                  label="Número comprobante"
                ></v-text-field>
                <span v-if="verDetalle == 1">
                  NÚMERO DE COMPROBANTE:<span class="green--text">{{
                    num_comprobante
                  }}</span>
                </span>
              </v-flex>
              <v-flex xs12 sm8 md8 lg8 18 v-if="verDetalle == 0">
                <v-autocomplete
                  v-if="verDetalle == 0"
                  :items="personas"
                  v-model="persona"
                  label="Clientes"
                ></v-autocomplete>
              </v-flex>
              <v-flex xs12 sm4 md4 lg4 x14 v-if="verDetalle == 1">
                <span>
                  CLIENTE:<span class="green--text">{{ persona.nombre }}</span>
                </span>
              </v-flex>
              <v-flex xs12 sm4 md4 lg4 x14>
                <v-text-field
                  v-if="verDetalle == 0"
                  v-model.number="impuesto"
                  label="Impuesto"
                ></v-text-field>
                <span v-if="verDetalle == 1">
                  IMPUESTO:<span class="green--text">{{ impuesto }}</span>
                </span>
              </v-flex>
              <v-flex xs12 sm8 md8 lg8 x8 v-if="verDetalle == 0">
                <v-text-field
                  v-model="codigos"
                  label="Códigos"
                  @keyup.enter="buscarCodigo()"
                ></v-text-field>
              </v-flex>
              <v-flex xs12 sm2 md2 lg2 x2 v-if="verDetalle == 0">
                <v-btn
                  small
                  fab
                  dark
                  color="teal"
                  @click="mostrarModalArticulo()"
                  class="mr-1"
                >
                  <v-icon title="Buscar Articulo" dark>list</v-icon>
                </v-btn>
                <v-btn small dark color="secondary" @click="buscarCodigo()">
                  Agregar
                </v-btn>
              </v-flex>
              <v-flex xs12 sm2 md2 lg2 x12>
                <div
                  class="red--text"
                  v-text="errorArticulo"
                  v-show="errorArticulo"
                ></div>
              </v-flex>
              <v-flex xs12 sm12 md12 lg12 x12>
                <template>
                  <v-data-table
                    :headers="cabeceraDetalles"
                    :items="detalles"
                    hide-default-footer
                    class="elevation-1"
                    v-if="verDetalle == 0"
                  >
                    <template v-slot:item.borrar="{ item }">
                      <td v-if="verDetalle == 0">
                        <v-icon
                          small
                          class="mr-2"
                          title="Eliminar"
                          @click="eliminarDetalle(detalles, item)"
                        >
                          delete
                        </v-icon>
                      </td>
                    </template>
                    <template v-slot:item.subtotal="{ item }">
                      <td class="text-xs-center">
                        $
                        {{
                          (
                            item.cantidad * item.precio -
                            item.descuento
                          ).toFixed(2)
                        }}
                      </td>
                    </template>
                    <template v-slot:item.precio="{ item }">
                      <td v-if="verDetalle == 0" class="text-xs-center">
                        <v-text-field v-model="item.precio"></v-text-field>
                      </td>
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.precio.toFixed(2) }}
                      </td>
                    </template>
                    <template v-slot:item.descuento="{ item }">
                      <td v-if="verDetalle == 0" class="text-xs-center">
                        <v-text-field v-model="item.descuento"></v-text-field>
                      </td>
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.descuento }}
                      </td>
                    </template>
                    <template v-slot:item.cantidad="{ item }">
                      <td v-if="verDetalle == 0" class="text-xs-center">
                        <v-text-field v-model="item.cantidad"></v-text-field>
                      </td>
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.cantidad }}
                      </td>
                    </template>

                    <template slot="no-data">
                      <h3>No hay articulos agregados al detalle</h3>
                    </template>
                  </v-data-table>
                  <v-data-table
                    :headers="cabeceraDetallesF"
                    :items="detalles"
                    hide-default-footer
                    class="elevation-1"
                    v-if="verDetalle == 1"
                  >
                    <template v-slot:item.subtotal="{ item }">
                      <td class="text-xs-center">
                        $
                        {{
                          (
                            item.cantidad * item.precio -
                            item.descuento
                          ).toFixed(2)
                        }}
                      </td>
                    </template>
                    <template v-slot:item.precio="{ item }">
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.precio.toFixed(2) }}
                      </td>
                    </template>
                    <template v-slot:item.descuento="{ item }">
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.descuento }}
                      </td>
                    </template>
                    <template v-slot:item.cantidad="{ item }">
                      <td v-if="verDetalle == 1" class="text-xs-center">
                        {{ item.cantidad }}
                      </td>
                    </template>
                  </v-data-table>
                  <template>
                    <v-flex>
                      <v-card>
                        <v-card-text class="text-right">
                          <strong>Total Parcial:</strong>
                          $
                          {{
                            (totalParcial = total - totalImpuesto).toFixed(2)
                          }}
                          <br />
                          <strong>Total Impuesto:</strong>
                          $
                          {{
                            (totalImpuesto = (
                              (total * impuesto) /
                              (1 + impuesto)
                            ).toFixed(2))
                          }}

                          <br />
                          <strong>Total Neto:</strong> $
                          {{ (total = calcularTotal).toFixed(2) }}
                          <br />
                          <div
                            class="red--text"
                            v-for="v in validaMensaje"
                            :key="v"
                            v-text="v"
                          ></div>
                        </v-card-text>
                        <v-card-actions>
                          <v-row align="center" justify="center">
                            <v-btn
                              color="secondary"
                              class="mb-5 mt-0 mr-5"
                              @click.native="ocultarNuevo()"
                              >Cerrar</v-btn
                            >
                            <v-btn
                              color="success"
                              class="mb-5 mt-0"
                              v-if="verDetalle == 0"
                              @click.native="guardar()"
                              >Guardar</v-btn
                            >
                          </v-row>
                        </v-card-actions>
                      </v-card>
                    </v-flex>
                  </template>
                </template>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </template>
    </v-flex>
  </v-layout>
</template>

<script>
import axios from "axios";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
export default {
  data() {
    return {
      dialog: false,
      search: "",
      ventas: [],
      dialogDelete: false,
      headers: [
        { text: "Opciones", value: "opciones", sortable: true },
        { text: "Usuario", value: "usuario.nombre", sortable: true },
        { text: "Cliente", value: "persona.nombre", sortable: true },
        {
          text: "Tipo de comprobante",
          value: "tipo_comprobante",
          sortable: true,
        },
        {
          text: "Serie de comprobante",
          value: "serie_comprobante",
          sortable: false,
        },
        {
          text: "Número de comprobante",
          value: "num_comprobante",
          sortable: false,
        },
        { text: "Fecha", value: "createdAt", sortable: false },
        { text: "Impuesto", value: "impuesto", sortable: false },
        { text: "Total", value: "total", sortable: false },
        { text: "Estado", value: "estado" },
      ],
      _id: "",
      persona: "",
      personas: [],
      tipo_comprobante: "",
      comprobantes: ["BOLETA", "FACTURA", "TICKET"],
      serie_comprobante: "",
      num_comprobante: "",
      impuesto: 0.18,
      codigos: "",
      cabeceraDetalles: [
        { text: "Borrar", value: "borrar", sortable: false },
        { text: "Artículo", value: "articulo", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub total", value: "subtotal", sortable: false },
      ],
      cabeceraDetallesF: [
        { text: "Artículo", value: "articulo", sortable: false },
        { text: "Cantidad", value: "cantidad", sortable: false },
        { text: "Precio", value: "precio", sortable: false },
        { text: "Descuento", value: "descuento", sortable: false },
        { text: "Sub total", value: "subtotal", sortable: false },
      ],
      detalles: [],
      verNuevo: 0,
      errorArticulo: null,
      total: 0,
      totalParcial: 0,
      totalImpuesto: 0,
      articulos: [],
      texto: "",
      cabeceraArticulo: [
        { text: "Seleccionar", value: "seleccionar", sortable: false },
        { text: "Código", value: "codigos", sortable: false },
        { text: "Nombre", value: "nombre", sortable: false },
        { text: "Categoría", value: "categoria.nombre", sortable: false },
        { text: "Cantidad", value: "stock", sortable: false },
        { text: "Precio venta", value: "precio_venta", sortable: false },
        { text: "Descripción", value: "descripcion", sortable: false },
        { text: "Estado", value: "estado", sortable: false },
      ],
      valida: 0,
      comprobanteModal: 0,
      verDetalle: 0,
      validaMensaje: [],
      adModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
      fecha: "",
      inputN: 0,
    };
  },
  computed: {
    calcularTotal: function() {
      let resultado = 0.0;
      for (var i = 0; i < this.detalles.length; i++) {
        resultado =
          resultado +
          (this.detalles[i].cantidad * this.detalles[i].precio -
            this.detalles[i].descuento);
      }
      resultado.toFixed(2);
      return resultado;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.listar();
    this.selectPersona();
  },

  methods: {
    crearPDF() {
      var quotes = document.getElementById("factura");
      html2canvas(quotes).then(function(canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 210;
        var pageHeight = 295;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("ComprobanteVenta.pdf");
      });
    },
    mostrarComprobante(item) {
      this.limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante = item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.fecha = item.createdAt;
      this.persona = item.persona;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
      this.comprobanteModal = 1;
    },
    ocultarComprobante() {
      this.comprobanteModal = 0;
      this.limpiar();
    },
    selectPersona() {
      let me = this;
      let personaArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("persona/listClientes", configuracion)
        .then(function(response) {
          personaArray = response.data;
          personaArray.map(function(x) {
            me.personas.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    agregarDetalle(data) {
      this.errorArticulo = null;
      if (this.encuentra(data._id) == true) {
        this.errorArticulo = "El articulo ya ha sido agregado";
      } else {
        this.detalles.push({
          _id: data._id,
          articulo: data.nombre,
          cantidad: 1,
          precio: data.precio_venta,
          descuento: 0,
        });
        this.codigos = "";
      }
    },
    eliminarDetalle(err, item) {
      let i = err.indexOf(item);
      if (i != -1) {
        err.splice(i, 1);
      }
    },
    encuentra(id) {
      let sw = 0;
      for (var i = 0; i < this.detalles.length; i++) {
        if (this.detalles[i]._id == id) {
          sw = true;
        }
      }
      return sw;
    },
    buscarCodigo() {
      let me = this;
      me.errorArticulo = null;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("articulo/queryCodigo?codigos=" + this.codigos, configuracion)
        .then(function(response) {
          me.agregarDetalle(response.data);
        })
        .catch(function(error) {
          me.errorArticulo = "No se a encontrado el artículo";
        });
    },
    listarArticulos() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("articulo/list?valor=" + this.texto, configuracion)
        .then(function(response) {
          me.articulos = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/query?_id=" + id, configuracion)
        .then(function(response) {
          me.detalles = response.data.detalles;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    verIngreso(item) {
      this.limpiar();
      this.tipo_comprobante = item.tipo_comprobante;
      this.serie_comprobante = item.serie_comprobante;
      this.num_comprobante = item.num_comprobante;
      this.persona = item.persona;
      this.impuesto = item.impuesto;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetalle = 1;
      console.log(this.persona);
    },
    mostrarModalArticulo() {
      this.dialog = true;
      this.listarArticulos();
    },
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/list", configuracion)
        .then(function(response) {
          me.ventas = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    limpiar() {
      this._id = "";
      this.tipo_comprobante = "";
      this.serie_comprobante = "";
      this.num_comprobante = "";
      this.impuesto = 0.18;
      this.codigos = "";
      this.total = "";
      this.persona = "";
      this.totalParcial = "";
      this.totalImpuesto = "";
      this.detalles = [];
      this.verNuevo = 0;
      this.valida = 0;
      this.validaMensaje = [];
      this.verDetalle = 0;
    },
    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.persona) {
        this.validaMensaje.push("El Proveedor no existe");
      }
      if (!this.tipo_comprobante) {
        this.validaMensaje.push("Seleccione un tipo de comprobante");
      }
      if (!this.num_comprobante) {
        this.validaMensaje.push("Ingrese el número de comprobante");
      }
      if (!this.impuesto || this.impuesto < 0 || this.impuesto > 1) {
        this.validaMensaje.push("Ingrese un impuesto valido");
      }
      if (!isNaN(this.impuesto) == false) {
        this.validaMensaje.push("El impuesto debe contener solo numeros");
      }
      if (this.detalles.length <= 0) {
        this.validaMensaje.push("Ingrese al menos un articulo al detalle");
      }
      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },
    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      if (this.validar()) {
        return;
      }
      //Codigo de guardar
      axios
        .post(
          "venta/add",
          {
            persona: this.persona,
            usuario: this.$store.state.usuario._id,
            tipo_comprobante: this.tipo_comprobante,
            serie_comprobante: this.serie_comprobante,
            num_comprobante: this.num_comprobante,
            impuesto: this.impuesto,
            total: this.total.toFixed(2),
            detalles: this.detalles,
          },
          configuracion
        )
        .then(function(response) {
          me.limpiar();
          me.close();
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activarDesactivarMostrar(accion, item) {
      this.adModal = 1;
      this.adNombre = item.num_comprobante;
      this.adId = item._id;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else {
        this.adModal = 0;
      }
    },
    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("venta/activate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put("venta/deactivate", { _id: this.adId }, configuracion)
        .then(function(response) {
          me.adModal = 0;
          me.adAccion = 0;
          me.adNombre = "";
          me.adId = "";
          me.listar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    activarDesactivarCerrar() {
      this.adModal = 0;
    },
    close() {
      this.dialog = false;
    },
  },
};
</script>
