<template>
    <v-layout align-start>
      <v-flex>
        <v-data-table
      :headers="headers"
      :items="articulos"
      :search="search"
      sort-by="calories"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title>Articulos</v-toolbar-title>
          <v-divider
            class="mx-4"
            inset
            vertical
          ></v-divider>
          <v-spacer></v-spacer>
              <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="Búsqueda" single-line hide-details></v-text-field>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn class="ml-5 mb-2" color="success"
                      ><v-icon title="Descargar PDF" @click="crearPDF()"
                        >picture_as_pdf</v-icon
                      ></v-btn>
              
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                Nuevo
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="hidline">Nuevo Registro</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-text-field
                        v-model="codigos"
                        label="Codigo"
                      ></v-text-field>
                    </v-col>
                     <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-select
                        :items="categorias"
                        v-model="categoria"
                        label="Categorias"
                      ></v-select>
                    </v-col>
                     <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-text-field
                        v-model="nombre"
                        label="Nombre"
                      ></v-text-field>
                    </v-col>
                     <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-text-field
                        type="number"
                        v-model="stock"
                        label="Cantidad"
                      ></v-text-field>
                    </v-col>
                     <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-text-field
                        v-model="precio_venta"
                        label="Precio de Venta"
                      ></v-text-field>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                      md="6"
                    >
                      <v-text-field
                        v-model="descripcion"
                        label="Descripcion"
                      ></v-text-field>
                    </v-col>
                       <v-col
                      cols="12"
                      sm="12"
                      md="12"
                      v-show="valida"
                    >
                      <div  class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="secondary"
                  text
                  @click="close"
                >
                  Cancelar
                </v-btn>
                <v-btn
                  color="success"
                  text
                  @click="guardar"
                >
                  Guardar
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline">Seguro que quieres borrar este item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" text @click="close">Cancel</v-btn>
                <v-btn color="success" text @click="guardar">Guardar</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="adModal" max-width="290px">
              <v-card>
                  <v-card-title class="headline" v-if="adAccion==1">
                        Activar Item
                  </v-card-title>
                  <v-card-title class="headline" v-if="adAccion==2">
                        Desactivar Item
                  </v-card-title>
                  <v-card-text>
                        Estas a punto de <h4 v-if="adAccion==1">Activar</h4> <h4 v-if="adAccion==2">Desactivar</h4>el item {{adNombre}}
                  </v-card-text>
                  <v-card-actions>
                     <v-spacer></v-spacer>
                      <v-btn @click="activarDesactivarCerrar()" text color="secondary" >
                          Cancelar
                      </v-btn>
                      <v-btn v-if="adAccion==1" @click="activar()" text color="success" >
                          Activar
                      </v-btn>
                      <v-btn v-if="adAccion==2" @click="desactivar()" text color="error" >
                          Desactivar
                      </v-btn>
                  </v-card-actions>
              </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.estado="{item}">
              <h4 v-if="item.estado" class="green--text">Activo</h4>
              <h4 v-else class="red--text">Inactivo</h4>
      </template>
      <template v-slot:item.opciones="{ item }">
      <td class="justify-center layout px-0">
        <v-icon
          small
          title="Editar"
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>
        <template v-if="item.estado">
            <v-icon
              small
              title="Desactivar"
              @click="activarDesactivarMostrar(2,item)"
            >
              block
            </v-icon>
        </template>
        <template v-else>
            <v-icon
              small
              title="Activar"
              @click="activarDesactivarMostrar(1,item)"
            >
              check
            </v-icon>
        </template>
      </td>
      </template>
    </v-data-table>
      </v-flex>
    </v-layout>
</template>

<script>
import axios from 'axios';
import jsPDF from 'jspdf'
import autoTable from 'jspdf-autotable'
export default {
    data(){
        return{
            dialog: false,
            search:'',
            articulos:[],
            dialogDelete: false,
                headers: [
                { text: 'Opciones', value: 'opciones', sortable: false },
                { text: 'Codigo', value: 'codigos', sortable: false },
                { text: 'Nombre', value: 'nombre' },
                { text: 'Categorias', value: 'categoria.nombre', sortable: false },
                { text: 'Cantidad', value: 'stock', sortable: false },
                { text: 'Precio de Venta', value: 'precio_venta', sortable: false },
                { text: 'Descripción', value: 'descripcion' },
                { text: 'Estado', value: 'estado'},      
              ],
                desserts: [],
                editedIndex: -1,
                _id:'',
                codigos:'',
                nombre:'',
                categoria:'',
                categorias:[],
                stock:0,
                precio_venta:'',
                descripcion:'',
                valida:0,
                validaMensaje:[],
                adModal:0,
                adAccion:0,
                adNombre:'',
                adId:'',
        }
    },

  watch: {
    dialog (val) {
      val || this.close()
    }
  },

  created () {
    this.listar()
    this.selectCategoria();
  },

  methods: {
    crearPDF(){ 
      var columns=[
        {title:"Nombre",dataKey:"nombre"},
        {title:"Codigos",dataKey:"codigos"},
        {title:"Categorías",dataKey:"categoria"},
        {title:"Stock",dataKey:"stock"},
        {title:"Precio Venta",dataKey:"precio_venta"}
      ];
      var rows=[];
      this.articulos.map(function(x){
        rows.push({
          nombre:x.nombre,
          codigos:x.codigos,
          categoria:x.categoria.nombre,
          stock:x.stock,
          precio_venta:x.precio_venta
        })
      })

      const doc = new jsPDF('p','pt');
      doc.autoTable(columns,rows,{
        margin:{top:60},
        addPageContent:function(data){
          doc.text("Lista de Artículos",40,30)
        }
      })
      doc.save("Articulos.pdf");
    },
    selectCategoria(){
      let me=this;
      var categoriaArray = [];
      let header={'Token':this.$store.state.token};
      let configuracion={headers:header}
      axios.get('categoria/list',configuracion).then(function(response){
        categoriaArray=response.data;
        categoriaArray.map(function(x){
          me.categorias.push({text:x.nombre,value:x._id})
        })
      }).catch(function(error){
        console.log(error);
      })
    },
    listar(){
      let me=this;
      let header={'Token':this.$store.state.token};
      let configuracion={headers:header}
      axios.get('articulo/list',configuracion).then(function(response){
        me.articulos=response.data;
      }).catch(function(error){
        console.log(error);
      })
    },
    limpiar(){
        this._id='';
        this.categoria='';
        this.codigos='';
        this.nombre='';
        this.stock=0;
        this.precio_venta=0;
        this.descripcion='';
        this.valida=0;
        this.validaMensaje=[];
        this.editedIndex=-1;
    }
    ,validar(){
          this.valida=0;
          this.validaMensaje=[];
          if(!this.categoria){
              this.validaMensaje.push('Selecciona la categoría')
          }
          if(this.codigos.length > 64){
              this.validaMensaje.push('El código del articulo no debe tener mas 64 caracteres')
          }
          if(this.nombre.length < 1 || this.nombre.length > 50){
              this.validaMensaje.push('El nombre del articulo debe ser de entre 1-50 caracteres')
          }
          if(this.stock.length < 0){
              this.validaMensaje.push('Ingrese una cantidad Valida')
          }
          if(this.stock.length < 0){
              this.validaMensaje.push('Ingrese un precio valido')
          }
          if(this.descripcion.length > 255){
              this.validaMensaje.push('La descripcion de la categoria debe ser de menor de 255 caracteres')
          }
          if(this.validaMensaje.length){
              this.valida=1;
          }
          return this.valida;
    },
    guardar(){
        let me=this;
        let header={'Token':this.$store.state.token};
        let configuracion={'headers':header}
        if(this.validar()){
          return;
        }
        if(this.editedIndex >-1){
          //Codigo de editar
            axios.put('articulo/update',{
              '_id':this._id,
              'codigos':this.codigos,
              'categoria':this.categoria,
              'nombre':this.nombre,
              'stock':this.stock,
              'precio_venta':this.precio_venta,
              'descripcion':this.descripcion},configuracion)
            .then(function(response){
                me.limpiar();
                me.close();
                me.listar();
            }).catch(function(error){
                console.log(error)
            })
        }else{
          //Codigo de guardar
            axios.post('articulo/add',{
              'codigos':this.codigos,
              'categoria':this.categoria,
              'nombre':this.nombre,
              'stock':this.stock,
              'precio_venta':this.precio_venta,
              'descripcion':this.descripcion
              },configuracion)
            .then(function(response){
                me.limpiar();
                me.close();
                me.listar();
            }).catch(function(error){
                console.log(error)
            })
        }
    },
    editItem (item) {
        this._id=item._id;
        this.categoria=item.categoria._id;
        this.codigos=item.codigos;
        this.nombre=item.nombre;
        this.stock=item.stock;
        this.precio_venta=item.precio_venta;
        this.descripcion=item.descripcion;
        this.dialog = true;
        this.editedIndex=1;
    },

    activarDesactivarMostrar (accion,item) {
        this.adModal=1;
        this.adNombre=item.nombre;
        this.adId=item._id;
        if(accion == 1){
          this.adAccion=1;
        }else if(accion == 2){
          this.adAccion=2;
        }else{
          this.adModal=0;
        }
    },
    activar(){
      let me=this;
      let header={'Token':this.$store.state.token};
      let configuracion={headers:header}
       axios.put('articulo/activate',{"_id":this.adId},configuracion)
            .then(function(response){
                me.adModal=0;
                me.adAccion=0;
                me.adNombre='';
                me.adId='';
                me.listar();
            }).catch(function(error){
                console.log(error)
            })
    },
    desactivar(){
      let me=this;
      let header={'Token':this.$store.state.token};
      let configuracion={headers:header}
       axios.put('articulo/deactivate',{"_id":this.adId},configuracion)
            .then(function(response){
                me.adModal=0;
                me.adAccion=0;
                me.adNombre='';
                me.adId='';
                me.listar();
            }).catch(function(error){
                console.log(error)
            })
    },
    activarDesactivarCerrar(){
      this.adModal=0;
    },
    close () {
        this.dialog = false;
        this.limpiar();
    },

  },
}
</script>
