import Vue from 'vue'
import store from '../store/index'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Categoria from '../components/Categoria.vue'
import Cliente from '../components/Cliente.vue'
import Proveedor from '../components/Proveedores.vue'
import Ingreso from '../components/Ingresos.vue'
import Venta from '../components/Venta.vue'
import Articulo from '../components/Articulo.vue'
import Usuario from '../components/Usuario.vue'
import Login from '../components/Login.vue'
 
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{
      administrador:true,
      almacenador:true,
      vendedor:true
    }
  },
  {
    path: '/login',
    name: 'login',
    component:Login,
    meta:{
      libre:true
    }
  },
  {
    path: '/categoria',
    name: 'categoria',
    component: Categoria,
    meta:{
      administrador:true,
      almacenador:true,
    }
  },
  {
    path: '/articulo',
    name: 'articulo',
    component: Articulo,
    meta:{
      administrador:true,
      almacenador:true,
    }
  },
  {
    path: '/cliente',
    name: 'cliente',
    component: Cliente,
    meta:{
      administrador:true,
      vendedor:true,
    }
  },
  {
    path: '/proveedor',
    name: 'proveedor',
    component: Proveedor,
    meta:{
      administrador:true,
      almacenero:true,
    }
  },
  {
    path: '/ingreso',
    name: 'ingreso',
    component: Ingreso,
    meta:{
      administrador:true,
      almacenero:true,
    }
  },
  {
    path: '/venta',
    name: 'venta',
    component: Venta,
    meta:{
      administrador:true,
      vendedor:true,
    }
  },
  {
    path: '/usuario',
    name: 'usuario',
    component:  Usuario,
    meta:{
      administrador:true,

    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  if(to.matched.some(record=>record.meta.libre)){
    next();
  }else if(store.state.usuario && store.state.usuario.rol == 'Administrador' ){
    if(to.matched.some(record=>record.meta.administrador)){
      next();
    }
  }else if(store.state.usuario && store.state.usuario.rol == 'Vendedor' ){
    if(to.matched.some(record=>record.meta.vendedor)){
      next();
    }
  }else if(store.state.usuario && store.state.usuario.rol == 'Almacenador' ){
    if(to.matched.some(record=>record.meta.almacenador)){
      next();
    }
  }else{
    next({name:'login'})
  }   
})

export default router
