<template>
  <v-container grid-list-m cols="12" sm="6" offset-sm="3">
    <v-layout row>
      <v-card min-width="440px" class="mr-5">
        <v-flex xs12 sm12 md12>
          <div class="ct-chart">
            <canvas id="myChart"> </canvas>
          </div>
        </v-flex>
      </v-card>
      <template>
        <v-card min-width="440px">
          <v-card-title>
            Balance
            <v-divider class="mx-4" inset vertical></v-divider>
          </v-card-title>
          <v-card-text>
            <template>
              <v-simple-table dense>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">
                        Opciones
                      </th>
                      <th class="text-right">
                        Total
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>Ingreso</td>
                      <td class="green--text text-right">$ {{ venta }}</td>
                    </tr>
                    <tr>
                      <td>Egreso</td>
                      <td class="red--text text-right">$ {{ ingreso }}</td>
                    </tr>
                    <tr>
                      <td>Total Ganancia</td>
                      <td v-if="venta < ingreso" class="red--text text-right">
                        $ {{ (totalParcial = (venta - ingreso).toFixed(2)) }}
                      </td>
                      <td v-else class="green--text text-right">
                        $ {{ (totalParcial = (venta - ingreso).toFixed(2)) }}
                      </td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </template>
          </v-card-text>
        </v-card>
      </template>
    </v-layout>
    <v-layout row wrap>
      <template>
        <v-card min-width="440px" class="mt-5 mr-5">
          <v-card-title>
            Precio de Dólar
            <v-divider class="mx-4" inset vertical></v-divider>
          </v-card-title>
          <v-card-text class="mt-5">
            <h1 class="mb-3">{{ fecha }}</h1>
            <h1 class="mt-2">{{ dolar }} Bs</h1>
          </v-card-text>
        </v-card>
      </template>
      <template>
        <v-card v-if="cambiar == 0" min-width="440px" class="mt-5">
          <v-card-title>
            Convertidor
          </v-card-title>
          <v-card-text>
            <span>Elige la moneda que deseas convertir</span><br />
            <v-btn class="mr-2" color="success" @click="cambioDeConvertidorD()"
              >Dolares</v-btn
            >
            <v-btn color="secondary" @click="cambioDeConvertidorB()"
              >Bolivares</v-btn
            >
          </v-card-text>
          <v-card-actions class=" mr-4"> </v-card-actions>
        </v-card>
        <v-card v-if="cambiar == 1" min-width="440px" class="mt-5">
          <v-card-title>
            Convertidor Dolares
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-btn
              @click="cambioDeConvertidorD()"
              class="mr-5"
              title="Cambiar convertidor"
              color="success"
              >Cambiar</v-btn
            >
            <v-btn
              @click="regresar()"
              title="Regresar a elegir convertidor"
              color="secondary"
              ><v-icon>logout</v-icon></v-btn
            >
          </v-card-title>
          <v-card-text>
            <v-text-field
              label="Ingrese cantidad a convertir de dolares"
              v-model="convertidorDolar"
              type="number"
            ></v-text-field>
            <h1>{{ (dolar * convertidorDolar).toFixed(2) }} Bs</h1>
          </v-card-text>
        </v-card>
        <v-card v-if="cambiar == 2" min-width="440px" class="mt-5">
          <v-card-title>
            Convertidor Bolivares
            <v-divider class="mx-4" inset vertical></v-divider>
            <v-btn
              @click="cambioDeConvertidorB()"
              title="Cambiar convertidor"
              class="mr-2"
              color="success"
              >Cambiar</v-btn
            >
            <v-btn
              @click="regresar()"
              title="Cambiar convertidor"
              color="secondary"
              ><v-icon>logout</v-icon></v-btn
            >
          </v-card-title>
          <v-card-text>
            <v-text-field
              type="number"
              label="Ingrese cantidad a convertir de dolares"
              v-model="convertidorBolivar"
            ></v-text-field>
            <h1>{{ (convertidorBolivar / dolar).toFixed(4) }} $</h1>
          </v-card-text>
        </v-card>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import Chart from "chart.js";
export default {
  data() {
    return {
      valores: [],
      meses: [],
      totales: [],
      dolar: [],
      fecha: [],
      totalIngreso: [],
      totalVenta: [],
      venta: "",
      ingreso: "",
      totalParcial: "",
      convertidorDolar: "",
      convertidorBolivar: "",
      cambiar: 0,
    };
  },
  methods: {
    listar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/grafico", configuracion)
        .then(function(response) {
          me.valores = response.data;
          me.graficar();
          me.listarVenta();
          me.listarIngreso();
          me.getDolar();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    cambioDeConvertidorD() {
      if (this.cambiar == 0) {
        this.cambiar = 1;
      } else {
        this.cambiar = 2;
      }
    },
    cambioDeConvertidorB() {
      if (this.cambiar == 0) {
        this.cambiar = 2;
      } else {
        this.cambiar = 1;
      }
    },
    regresar() {
      this.cambiar = 0;
    },
    listarIngreso() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("ingreso/grafico", configuracion)
        .then(function(response) {
          me.ingreso = response.data;
          me.totalidadIngreso();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    listarVenta() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("venta/grafico", configuracion)
        .then(function(response) {
          me.ventas = response.data;
          me.totalidadVenta();
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    totalidadIngreso() {
      let me = this;
      me.ingreso.map(function(x) {
        me.totalIngreso.push(x.total);
        me.ingreso = me.totalIngreso[0];
      });
    },
    totalidadVenta() {
      let me = this;
      me.ventas.map(function(x) {
        me.totalVenta.push(x.total);
        me.venta = me.totalVenta[0];
      });
    },
    graficar() {
      let me = this;
      let mesn = "";
      me.valores.map(function(x) {
        switch (parseInt(x._id.mes)) {
          case 1:
            mesn = "Enero";
            break;
          case 2:
            mesn = "Febrero";
            break;
          case 3:
            mesn = "Marzo";
            break;
          case 4:
            mesn = "Abril";
            break;
          case 5:
            mesn = "Mayo";
            break;
          case 6:
            mesn = "Junio";
            break;
          case 7:
            mesn = "Julio";
            break;
          case 8:
            mesn = "Agosto";
            break;
          case 9:
            mesn = "Setiembre";
            break;
          case 10:
            mesn = "Octubre";
            break;
          case 11:
            mesn = "Noviembre";
            break;
          case 12:
            mesn = "Diciembre";
            break;
          default:
            mesn = "error";
        }

        me.meses.push(mesn + "-" + x._id.year);
        me.totales.push(x.total);
      });
      var ctx = document.getElementById("myChart");
      var myChart = new Chart(ctx, {
        type: "bar",
        data: {
          labels: me.meses,
          datasets: [
            {
              label: "Ventas de los últimos 12 meses",
              data: me.totales,
              backgroundColor: [
                "rgba(255, 99, 132, 0.2)",
                "rgba(54, 162, 235, 0.2)",
                "rgba(255, 206, 86, 0.2)",
                "rgba(75, 192, 192, 0.2)",
                "rgba(153, 102, 255, 0.2)",
                "rgba(255, 159, 64, 0.2)",
                "rgba(255, 99, 132, 0.2)",
                "rgba(54, 162, 235, 0.2)",
                "rgba(255, 206, 86, 0.2)",
                "rgba(75, 192, 192, 0.2)",
                "rgba(153, 102, 255, 0.2)",
                "rgba(255, 159, 64, 0.2)",
              ],
              borderColor: [
                "rgba(255, 99, 132, 1)",
                "rgba(54, 162, 235, 1)",
                "rgba(255, 206, 86, 1)",
                "rgba(75, 192, 192, 1)",
                "rgba(153, 102, 255, 1)",
                "rgba(255, 159, 64, 1)",
                "rgba(255, 99, 132, 1)",
                "rgba(54, 162, 235, 1)",
                "rgba(255, 206, 86, 1)",
                "rgba(75, 192, 192, 1)",
                "rgba(153, 102, 255, 1)",
                "rgba(255, 159, 64, 1)",
              ],
              borderWidth: 1,
            },
          ],
        },
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true,
                },
              },
            ],
          },
        },
      });
    },
    getDolar() {
      let me = this;
      axios
        .get("https://s3.amazonaws.com/dolartoday/data.json")
        .then(function(response) {
          me.dolar = response.data.USD.transferencia;
          me.fecha = response.data._timestamp.fecha_nice;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
  },
  mounted() {
    this.listar();
  },
};
</script>
